*, *:before, *:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  background: $app-background;
  font-family: $app-font-stack;
  font-weight: 300;
  color: $app-color;
  overflow: hidden;
}

// remove blue glow on focused elements
:focus {
  outline-color: transparent;
  outline-style: none;
}

// remove input spinner arrows
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield;
}

.message {
  margin-top: 0.9em;
  font-size: 4em;
  text-align: center;
  opacity: 1;
  filter: grayscale(0);
  transition: opacity 1s linear, filter 1s linear;
}

.dim {
  opacity: 0.5;
  filter: grayscale(75%);
}



